import React, { Component } from 'react';
import Timeline from './Timeline';
import sampleData from '../../utils/sampleData.json';
import { jsonToRecharts } from '../../rechartsUtils/dataConversion';

export class App extends Component {
  constructor() {
    super();
    this.state = {
      timelineLoading: true,
      timeline: jsonToRecharts(sampleData.bpi),
    };
  }

  componentDidMount() {
    fetch('api/timeline')
      .then(response => response.json())
      .then(response => jsonToRecharts(response))
      .then((data) => {
        this.setState({
          timelineLoading: false,
          timeline: data,
        });
      });
  }

  render() {
    const { timelineLoading, timeline } = this.state;
    return (
      <div className="App">
        <Timeline timelineLoading={timelineLoading} timeline={timeline} />
      </div>
    );
  }
}

export default App;



